// Menu items

.menu-list{
     width: 224px;

     .menu-item{
        margin-left: 0;
    }

}
.menu-poper {
    padding-bottom: 8px;
}
.menu-item{
    width: 100%;
    justify-content: flex-start;
    border-radius: 0;
    line-height: 1.8rem;

    &:hover{
        background: rgba(22, 24, 35, 0.03);

    }
    &.separate{
        border-top: 1px solid rgba(22, 24, 35, 0.12)
    }
   
}


// Header

.header{
    position: relative;
    height: 50px;
    margin-top: -8px;
    flex-shrink: 0;
}

.back-btn{
    width: 50px;
    height: 100%;
    background-color: transparent;
    cursor: pointer;
}

.title{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.wrapper-menu-item{
    overflow-y: auto;
}
